@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap');

* {
  font-family: 'Poppins', sans-serif;
}

#list-item input:checked ~ span {
    text-decoration: line-through;
    color: gray;
}

h1 {
  font-size: 60px;
  text-align: center;
  font-weight: 300;
}

.toggle-btn {
  display: flex;
  justify-content: end;
  margin: 20px 40px 0 0;
}

.s-1, .s-2, .s-3 {
  padding: 0 500px;
}

.s-1 {
  display: flex;
  justify-content: center;
}

.s-2 {
  display: flex;
  justify-content: center;
  transform: translateY(300%);
  visibility: hidden;
  transition: all .8s;
  transition-delay: .3s;
}

.s-2Show {
  visibility: visible;
  transform: translateY(0);
}

.list {
  height: 400px;
  padding: 10px 20px;
  overflow-y: scroll;
}

#list-item {
  display: flex;
  justify-content: space-between;
  background-color: white;
  box-shadow: 0 0 8px .5px rgb(214, 214, 214);
  border-radius: 5px;
  padding: 20px 30px;
  margin-bottom: 20px;
  gap: 5px;
  transition: all 1s;
}

.list-item-animate {
  scale: 1;
}

#list-item button {
  width: fit-content;
  padding: 0 5px;
  background-color: #FF4A4A;
  color: white;
  border: none;
  outline: none;
  border-radius: 3px;
  font-size: 20px;
  cursor: pointer;
}

#item {
  display: flex;
  align-items: center;
  gap: 10px;
  overflow: hidden;
}

#item input{
  scale: 1.7   ;
}

#item span {
  font-size: 20px;
}

.input-list {
  width: 100%;
  margin-bottom: 30px;
}

form {
  display: flex;
  justify-content: center;
  align-items: center;
}

form input {
  width: 500px;
  height: 60px;
  padding: 0 7px;
  border: 2px solid #5D7ADF;
  border-radius: 3px;
  font-size: 18px;
}

form input:focus {
  outline: none;
  box-shadow: 0 0 8px 1px rgb(94, 215, 255);
}

form button {
  height: 60px;
  width: 100px;
  border: none;
  background-color: #AF2CFF;
  color: white;
  font-size: 20px;
  border-radius: 5px;
  margin-left: 15px;
}

#btn {
  display: flex;
  align-items: center;
  position: absolute;
  justify-content: center;
  background-color: #AF2CFF;
  padding: 10px;
  height: 90px;
  width: 90px;
  border: none;
  border-radius: 100%;
  text-align: center;
  transition: all .5s;
  overflow-y: hidden;
}

.btnHidde {
  transform: translateY(-50%);
  opacity: 0;
  visibility: hidden;
}

#btn span {
  font-size: 35px;
  font-weight: 800;
  color: white;
  visibility: hidden;
  overflow: hidden;
  opacity: 0;
  transform: translateY(-200%);
  transition: all .8s;
}

#btn:hover{
  width: 350px;
  height: 90px;
  border-radius: 5px;
  gap: 20px;
}

#btn svg {
  transition: all .8s;
  position: absolute;
}

#btn:hover span{
  visibility: visible;
  opacity: 1;
  position: static;
  transform: translateY(0);
}

#btn:hover svg {
  position: static;
}

body {
  transition: all .2s;
}

/* Style untuk Dark Mode */
.body-dark{
  background-color: #202020;
  color: white;
}

.body-dark-span span {
  color: white;
}

.body-dark-listItem #list-item {
  background-color: #393939;
}

@media screen and (width < 576px) {

  .s-2 {
    padding: 0 30px;
  }

  .s-1, .s-3 {
    padding: 0;
  }

  #btn {
    height: 70px;
    width: 70px;
    animation: btn 1s forwards;
    animation-delay: .5s;
  }

  #btn span {
    font-size: 28px;
    font-weight: 500;
    animation: btn-span 1s forwards;
    animation-delay: 1s;
  }

  form {
    flex-direction: column;
    gap: 1rem;
  }

  form input {
    width: 80vw;
    height: 40px;
    padding: 0 7px;
    border: 2px solid #5D7ADF;
    border-radius: 3px;
    font-size: 15px;
  }

  form button {
    height: 40px;
    width: 100px;
    border: none;
    background-color: #AF2CFF;
    color: white;
    font-size: 18px;
    border-radius: 5px;
    margin-left: 15px;
  }

  #btn svg {
    animation: btn-svg 1s forwards;
    animation-delay: 1s;
  }

  @keyframes btn {
    to {
      width: 350px;
      height: 70px;
      border-radius: 5px;
      gap: 20px;
    }
  }

  @keyframes btn-span {
    to {
      visibility: visible;
      opacity: 1;
      position: static;
      transform: translateY(0);
    }
  }

  @keyframes btn-svg {
    to {
      position: static;
    }
  }

}

